.testimonials {
  background-color: $light-blue;

  &__content {
    width: max-content;
    margin: 0 auto;
    padding: 0 20px 120px;

    @include on-tablet {
      padding: 0 40px 148px;
    }

    @include on-desktop {
      width: max-content;
      padding: 150px 54px 186px;
    }

    @include on-design {
      padding: 150px 54px 148px;
    }
  }

  &__heading {
    margin: 0 auto 16px;
    font-family: Poppins, sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: $heading-color;
  }

  &__title {
    margin: 0 0 48px;
    font-family: Poppins, sans-serif;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: $title-color;

    @include on-tablet {
      width: 200px;
      margin: 0 0 56px;
    }
  }

  &__cards {
    @include on-desktop {
      display: flex;
    }
  }
}
